import Login from '@/components/Login'
import Home from '@/components/Home.vue'
import timeStamp from '@/components/timeStamp.vue'
import safetyReport from '@/components/safetyReport.vue'
import modalTest from '@/components/modalTest.vue'

export var routes = [
  {
    path: '/',
    name: 'Login',
    component: Login
  },
  {
    path: '/Home',
    component: Home
  },
  {
    path: '/timeStamp',
    name: 'timeStamp',
    component: timeStamp
  },
  {
    path: '/safetyReport',
    name: 'safetyReport',
    component: safetyReport
  },
  {
    path: '/modalTest',
    name: 'modalTest',
    component: modalTest
  }
]



