import Vue from 'vue'
import arriveModal from '../components/arriveModal'
import leaveModal from '../components/leaveModal'

export default {
  name: 'timeStamp',
  data () {
    return {
      showModal: false
    }
  },
  methods: {
	arrive: function(){
      this.showModal = true;
	  Vue.component('modal', arriveModal);
    },
    leave: function(){
      this.showModal = true;
	  Vue.component('modal', leaveModal);
    }
  }
}

